<template>
  <v-container
    id="regular-forms"
    fluid
    tag="section"
  >
    <v-form
      ref="form"
      lazy-validation
    >
      <crud
        ref="crud"
        @submit="submit"
      />
    </v-form>
  </v-container>
</template>

<script>
  import Crud from './Crud'
  export default {
    name: 'Edit',
    components: { Crud },
    data () {
      return {
        api: {},
        loaded: false,
      }
    },
    mounted: function () {
      this.$http.get(`/portal/point_of_services/${this.$route.params.id}`)
        .then(
          (response) => {
            this.loaded = true
            this.$refs.crud.setEditData(response.data)
          }
        )
        .catch(
          () => {
            this.$router.push({ name: 'point_of_service_all' })
          }
        )
    },
    methods: {
      submit: function () {
        if (!this.$refs.form.validate()) {
          return
        }

        let data = this.$refs.crud.api
        data.address.latitude = this.$refs.crud.position.lat
        data.address.longitude = this.$refs.crud.position.lng
        data.address.address_line1 = this.$refs.crud.street ? `${this.$refs.crud.street} ${this.$refs.crud.street_number}` : data.address.address_line1
        data.address.zip_code = this.$refs.crud.zipcode ? this.$refs.crud.zipcode : data.address.zip_code
        data.address.city = this.$refs.crud.city ? this.$refs.crud.city : data.address.city
        if (data.origin === 'manual' && (!data.address.latitude || !data.address.longitude || !data.address.address_line1 || !data.address.zip_code || !data.address.city)) {
          this.$toasted.global.error('Invalid address given')
          return
        }

        this.$http.post(
          `/portal/point_of_services/${this.$route.params.id}`,
          JSON.stringify(data)
        )
          .then((response) => {
            this.$toasted.global.success(response.data.message)
            this.$router.push({ name: 'point_of_service_all' })
          })
          .catch((error) => {
            this.$toasted.global.error(error.response.data.message)
          })
      },

    },
  }
</script>

